import React, { useState, useEffect, useCallback } from 'react';
import { Plus, Trash2, Check, X } from 'lucide-react';

interface ChecklistItem {
    id: string;
    text: string;
    completed: boolean;
    createdAt: number;
}

interface LifeChecklistProps {
    content: string;
    onUpdate: (newContent: string) => void;
    onAutoSave: () => void;
}

export const LifeChecklist: React.FC<LifeChecklistProps> = ({ content, onUpdate, onAutoSave }) => {
    const [items, setItems] = useState<ChecklistItem[]>([]);
    const [newItemText, setNewItemText] = useState<string | null>(null);
    const [isLoaded, setIsLoaded] = useState(false);

    // Parse initial content
    useEffect(() => {
        try {
            if (content) {
                const parsed = JSON.parse(content);
                if (Array.isArray(parsed)) {
                    setItems(parsed);
                } else {
                    // content might be markdown legacy
                    setItems([]);
                }
            }
        } catch (e) {
            console.warn("Could not parse checklist content as JSON", e);
            setItems([]);
        }
        setIsLoaded(true);
    }, []);

    // Sync changes to parent
    const updateParent = useCallback((newItems: ChecklistItem[]) => {
        const jsonString = JSON.stringify(newItems);
        onUpdate(jsonString);
    }, [onUpdate]);

    const addItem = () => {
        if (!newItemText || !newItemText.trim()) return;
        const newItem: ChecklistItem = {
            id: crypto.randomUUID(),
            text: newItemText.trim(),
            completed: false,
            createdAt: Date.now()
        };
        const newItems = [...items, newItem];
        setItems(newItems);
        setNewItemText('');
        updateParent(newItems);
        setTimeout(onAutoSave, 500);
    };

    const toggleItem = (id: string) => {
        const newItems = items.map(item =>
            item.id === id ? { ...item, completed: !item.completed } : item
        );
        setItems(newItems);
        updateParent(newItems);
        setTimeout(onAutoSave, 500); // Trigger save
    };

    const deleteItem = (id: string) => {
        const newItems = items.filter(item => item.id !== id);
        setItems(newItems);
        updateParent(newItems);
        setTimeout(onAutoSave, 500);
    };

    if (!isLoaded) return null;

    return (
        <div className="max-w-3xl mx-auto p-4 md:p-8">
            <h2 className="text-3xl font-bold text-graphite-900 dark:text-white mb-8">Master Life List</h2>

            <div className="space-y-1">
                {items.map(item => (
                    <div
                        key={item.id}
                        className="group flex items-center gap-3 p-3 rounded-lg hover:bg-graphite-100/50 dark:hover:bg-graphite-800/50 transition-colors duration-200"
                    >
                        <button
                            onClick={() => toggleItem(item.id)}
                            className={`flex-shrink-0 w-5 h-5 rounded border-2 flex items-center justify-center transition-all ${item.completed
                                    ? 'bg-pacific-500 border-pacific-500 text-white'
                                    : 'border-graphite-400 dark:border-graphite-600 hover:border-pacific-400 text-transparent'
                                }`}
                        >
                            {item.completed && <Check size={12} strokeWidth={4} />}
                        </button>

                        <span
                            className={`flex-1 text-base transition-all ${item.completed
                                    ? 'text-graphite-400 dark:text-graphite-500 line-through'
                                    : 'text-graphite-900 dark:text-white font-medium'
                                }`}
                        >
                            {item.text}
                        </span>

                        <button
                            onClick={() => deleteItem(item.id)}
                            className="p-1.5 text-graphite-400 hover:text-red-500 hover:bg-white/10 rounded-md opacity-0 group-hover:opacity-100 transition-all"
                        >
                            <X size={16} />
                        </button>
                    </div>
                ))}

                {/* Add New Item Row */}
                <div className="mt-2 pt-2">
                    {newItemText !== null ? (
                        <div className="flex items-center gap-3 p-3 rounded-lg bg-graphite-50 dark:bg-graphite-800/50">
                            <div className="w-5 h-5 rounded border-2 border-graphite-300 dark:border-graphite-600 border-dashed opacity-50" />
                            <input
                                autoFocus
                                type="text"
                                value={newItemText}
                                onChange={(e) => setNewItemText(e.target.value)}
                                onKeyDown={(e) => {
                                    if (e.key === 'Enter') addItem();
                                    if (e.key === 'Escape') setNewItemText(null);
                                }}
                                onBlur={() => {
                                    if (newItemText.trim()) addItem();
                                }}
                                placeholder="Type your new goal..."
                                className="flex-1 bg-transparent border-none focus:ring-0 p-0 text-base text-graphite-900 dark:text-white placeholder-graphite-400"
                            />
                            <button onClick={() => setNewItemText(null)} className="bg-transparent text-graphite-400 hover:text-graphite-600 dark:hover:text-white"><X size={16} /></button>
                        </div>
                    ) : (
                        <button
                            onClick={() => setNewItemText('')}
                            className="flex items-center gap-3 p-3 text-graphite-500 hover:text-graphite-800 dark:hover:text-white transition-colors group w-full text-left"
                        >
                            <Plus size={20} className="group-hover:scale-110 transition-transform" />
                            <span className="font-medium text-base ml-1">Add new item</span>
                        </button>
                    )}
                </div>
            </div>
        </div>
    );
};
